.search {
    all: revert;
    margin-bottom: 20px;
    display: flex;
    justify-content: stretch;
    align-items: center;
    border-radius: 10px;
    background-color: #334;
    position: relative;

    span {
        position: absolute;
        left: 20px;
        pointer-events: none;
    }

    textarea {
        flex-grow: 1;
        border: none;
        resize: none;
        background-color: transparent;
        height: 20px;
        padding: 20px;
        padding-left: calc(24px + 40px);
        font-size: 17px;
        font-family: inherit;

        &:focus-visible {
            border: none;
            outline: none;
        }

        &::placeholder {
            color: #778;
        }
    }
}

.level {
    background-color: #334;
    margin-bottom: 20px;
    border-radius: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    min-height: 70px;

    &.hidden {
        display: none;
    }
    
    &:last-child {
        margin-bottom: 0px;
    }

    h1 {
        margin: 0px;
        position: absolute;
        top: 10px;
        right: 10px;
        color: #556;
        font-size: 2em;
        user-select: none;
    }

    h3 {
        margin: 0px;
        position: absolute;
        bottom: 10px;
        right: 10px;
        color: #99a;
        font-size: 1.3em;
        user-select: none;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        .name {
            padding-right: 20px;
            margin-right: 20px;
            border-right: 1px solid #778;
        }

        .time, .milliseconds {
            font-family: 'Droid Sans Mono', 'Courier New', Courier, monospace;
        }

        .milliseconds {
            font-weight: 500;
            color: #778;
        }

        .trophy {
            height: 24px;
            margin-left: 7px;
        }
    }

    .img {
        width: 35%;
        background-position: center;
        aspect-ratio: 16 / 9;
        background-size: cover;
        border-radius: 10px;
        flex-shrink: 0;
        margin: 20px;
        margin-right: 0px;
    }

    .info {
        color: #dde;
        text-decoration: none;
        flex-grow: 1;
        padding-left: 20px;
        padding-right: 40px;
        z-index: 1;
        display: flex;
        justify-content: center;
        flex-direction: column;
        position: relative;
        align-self: stretch;
        overflow: hidden;
        border-top-right-radius: 20px;
        border-bottom-right-radius: 20px;

        h2 {
            color: #eef;
            font-size: 2em;
            margin: 0px;
            margin-bottom: 10px;
            white-space: nowrap;
            width: -webkit-fill-available;
            text-overflow: ellipsis;
            overflow: hidden;
        }
        
        p {
            font-size: 1.5em;
            margin: 0px;
            white-space: nowrap;
            width: -webkit-fill-available;
            text-overflow: ellipsis;
            overflow: hidden;
        }
    }

    .highlight {
        background-color: #f76;
        color: #223;
    }
}

.empty {
    background-color: #334;
    margin-bottom: 20px;
    padding: 20px;
    border-radius: 10px;

    &.hidden {
        display: none;
    }
}

@media screen and (max-width: 700px) {
    main {
        h1 {
            font-size: 1.3em !important;
        }

        h3 {
            font-size: 1em !important;

            .name {
                display: none !important;
            }

            .trophy {
                height: 19px !important;
            }
        }

        .info {
            h2 {
                font-size: 1.2em !important;
                margin-bottom: 5px !important;
            }
    
            p {
                font-size: 1em !important;
            }
        }
    }
}